@charset "utf-8";

.ico-housein {
    width: 60px;
    height: 60px;
    background:url('/images/home/ico_house_in.png') center/contain;
}
.ico-houseout {
    width: 60px;
    height: 60px;
    background:url('/images/home/ico_house_out.png') center/contain;
}

.section-inner {
    max-width: 1280px;
    margin: 0 auto;
}

.intro-home {
    height: 254px;
    background:url('/images/home/img_banner_home.jpg') center/cover;
}

.title-area {
    text-align: center;
    margin-bottom: 32px;

    .title-h2 {
        font-size: 32px;
        font-weight: bold;
        color: #222;
    }
}

.main-home {
    padding: 52px 0 80px;
}

.main-home-swiper {
    .swiper-slide {
        a {
            display: block;
            cursor: pointer;
        }

    }


    .box-thumb {
        position: relative;
    }

    .thumb-img {
        display: block;
        width: 100%;
    }

    .thumb-category {
        position: absolute;
        top: 10px;
        right: 10px;
    }

    .thumb-btn {
        position: absolute;
        bottom: 0;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 48px;
        border: solid 1px #eee;
        background-color: #222;
        text-align: center;

        .txt {
            font-size: 16px;
            font-weight: bold;
            color: #fff;
        }
    }
}


